.tags {
  display: none;
}

@media only screen and (min-width: 768px) {
  .tags {
    display: flex;
    margin: 20px 0;
  }

  .tags .tags__tag {
    border-bottom: 0;
    margin: -2px 0;
    display: inline-flex;
    align-items: center;
    padding: 0 28px;
    position: relative;
    color: #333;
  }

  .tags .tags__tag:after {
    content: '';
    height: 12px;
    width: 1px;
    background: #cdcdcd;
    display: block;
    position: absolute;
    right: 0;
  }

  .tags .tags__tag:first-child {
    padding-left: 0;
  }

  .tags .tags__tag:last-child:after {
    display: none;
  }

  .tags .tags__tag.active {
    color: #ED4545;
  }
}